#header{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  .top-bar{
    display: flex;
    justify-content: space-between;
    padding: 51px 0;
  }
  .logo{
    padding: 3px 0 0;
  }
}
.social{
  @extend %listreset;
  li{
    display: inline-block;
    padding-left: 17px;
  }
}
#nav {
  position: fixed;
  right: 40px;
  top: 0;
  bottom: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  font-size: 14px;
  &:before{
    content: "";
    width: 1px;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 6px;
    background: rgba(255,255,255,0.2);
  }
  ul{
    @extend %listreset;
    li{
      position: relative;
      margin-bottom: 15px;
      &:hover,
      &.active{
        .tooltip{
          display: block;
        }
        a{
          border: 1px solid $white;
          left: -1px;
          top: -1px;
        }
      }
    }
    a{
      width: 15px;
      height: 15px;
      display: block;
      position: relative;
      border-radius: 50%;
      &:before{
        width: 5px;
        height: 5px;
        background: $white;
        position: absolute;
        top: 4px;
        left: 4px;
        content: "";
        border-radius: 50%;
      }
    }
    .tooltip{
      position: absolute;
      right: 30px;
      top: -7px;
      white-space: nowrap;
      color: $secondary-color;
      display: none;
    }
  }
}