html, body{
  height: 100%;
  scroll-behavior: smooth;
}
.top-section{
  position: relative;
  .banner{
    min-height: 100vh;
    display: flex;
    align-items: center;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    .container{
      max-width: 1370px;
    }
    .row-holder{
      align-items: center;
    }
    h1{
      margin: 0 0 13px;
      span{
        color: $secondary-color;
      }
    }
    address{
      border-left: 1px solid $white;
      font-style: normal;
      padding: 15px 30px;
      margin-bottom: 30px;
    }
  }
  .lang-holder{
    position: absolute;
    bottom: 51px;
    left: 0;
    right: 0;
    .lang{
      font-size: 14px;
      text-transform: uppercase;
      @extend %listreset;
      li{
        display: inline-block;
        padding-right: 10px;
        a{
          color: $light-gray;
        }
        &.active{
          a{
            color: $white;
            font-weight: bold;
          }
        }
        &:hover{
          a{
            color: $white;
          }
        }
      }
    }
  }
}
.section{
  height: 100vh;
  padding: 40px 0;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  &.contact{
    padding: 0;
  }
  &.video{
    padding: 0;
    position: relative;
    .holder{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      padding: 40px 0;
    }
    .video{
      display: block;
      width: 100%;
    }
    video[poster]{
      height:100%;
      width:100%;
      background-size: cover;
    }
    .play{
      position: absolute;
      top:50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 186px;
      height: 186px;
      border: 1px solid $white;
      border-radius: 50%;
      cursor: pointer;
      &:after{
        content: "";
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 25.5px 0 25.5px 33px;
        border-color: transparent transparent transparent $white;
      }
    }
  }
}
.gallery{
  @extend %listreset;
  li {
    display: inline-block;
  }
}
.brands{
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  li{
    flex-grow: 1;
    min-width: 25%;
    padding-bottom: 30px;
  }
  a{
    display: block;
    &:hover{

    }
  }
}