.btn {
	display: inline-block;
	vertical-align: top;
	text-transform: uppercase;
	position: relative;
	span{
		padding: 15px 30px;
		display: inline-block;
		border: 1px solid $white;
		position: relative;
		z-index: 2;
	}
	&:before{
		transition: all 0.85s cubic-bezier(0.68, -0.55, 0.265, 1.55);
		content: '';
		width: 33px;
		height: 7px;
		background: $secondary-color;
		position: absolute;
		top: 50%;
		left: -15px;
		transform: translateY(-50%);
	}
	&:hover,
	&:focus {
		&:before{
			width: 100%;
		}
	}
}
